import React, {useState} from "react";

function Modal({active, setActive}){
  const [title, setTitle] = useState('');
  const [descr, setDescr] = useState('');


  function getTitle(e){
    setTitle(e.target.value);
  }

  function getDescription(e){
    setDescr(e.target.value);
  }

  function getFavoutites(){
    return JSON.parse(localStorage.getItem('favourites'))
  };

  function saveCustomRecipe(){
    let fav = getFavoutites() || [];

    const myOwnRecipe =
    {idMeal: Math.random(),
    strMeal: title,
    strMealThumb: null,
    strInstructions: descr,
    strYoutube: null
    }

    fav.push(myOwnRecipe);
    localStorage.setItem('favourites', JSON.stringify(fav))

    setTitle('');
    setDescr('');
    setActive(false);
  }


  return (
    <div className={active ? "custom active-custom" : "custom"} onClick={()=>setActive(false)}>
      <div className={active ? "custom__form active-custom" : "custom__form"} onClick={ e=> e.stopPropagation()}>
          <div className="close" onClick={()=>setActive(false)}>
            <span>x</span>
          </div>
          <h3>Add custom dish</h3>
          <div className="custom__inp">
            <input
              className="form-title"
              placeholder="Dish Title"
              value={title}
              onChange={getTitle}
              />
            <textarea
              className="form-descripton"
              placeholder="Dish descripton..."
              rows="5"
              cols="33"
              value={descr}
              onChange={getDescription}
              />
            <button className="form-btn" type="button" onClick={saveCustomRecipe} disabled={title.trim().length > 0 && descr.trim().length > 0 ? false : true}>Add custom Dish</button>
          </div>
      </div>
    </div>
  )

}

export default Modal